body {
  display: flex;
  align-content: center;
  background-image: url(black.png);
  background-image: no-repeat;
  background-position: center;
  background-size: cover;
  justify-content: center;
  font-size: 9px;
  font-family: 'Open Sans';
  color: yellow;
}

.App {
  height: 100vh;
  width: 80vw;
  display: flex;
}

a {
    text-decoration: none;
    color: white;
}

/* Header */

.header_container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; 
  width: 80vw;
  top: 5rem;
  position: relative;
}

.header_container h2 {
  font-size: 3rem;
  font-weight: bold;
}

.header_container ul {
  list-style: none;
}

.header_container ul li {
  display: inline-block;
  padding-right: 2rem;
}

.header_container ul li a {
  text-decoration: none;
  color: yellow;
  font-size: 2.5rem;
}

/* Home */
.home_container {
  display: flex;
  flex-direction: row;
  justify-content:space-between;
  position: relative;
  top: 12rem; 
}

.home_container .home_head_wrapper {
  position: relative;
  top:1rem;
  display: flex;
  flex-direction: column;
  width: 40vw;
}

.home_container .home_head_wrapper h1 {
  font-size: 3.5rem;
  font-family: "Montserrat";
}

.home_container .home_head_wrapper h1 span {
 color:yellow;
}

.home_container .home_head_wrapper p {
  position: relative;
  top:-1.5rem;
  font-size: 2rem;
  opacity: 0.4;
}

.home_container .image_container img {
  position: relative;
  height: 25rem;
  width: 18rem;
  top:5rem; 
}

.home_container ul {
  list-style-type: none;
}

.home_container li {
  display: inline;
  padding-right: 2rem;
}

.home_container li a {
  color: white;
  text-decoration: none;
  font-size: 2rem;
}


/* Work */
.projects_container {
    display: flex;
    flex-direction: column;
    position: relative;
    top: 18rem;
    width: 80vw;
}

.projects_container h1 {
  font-size: 4rem;
  font-family: 'Montserrat';
}

.projects_container .myWorks_container {
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 1rem;
}

.projects_container .myWorks_container img {
  height: 17rem;
  width: 17rem;
  box-shadow:5px 5px 2.5px yellow;
}

.projects_container .myWorks_container .title {
  font-size: 1.7rem;
}

.btn-yellow {
  font-size: 1.4vw;
  background-color: black;
  color: #F0F7EE !important;
}

.btn-yellow:hover {
  background-color: grey;
}

/* Contact */

.contact_container {
  display: flex;
  flex-direction: column;
  position: relative;
  top:20rem;
}

.contact_container h1 {
  font-size: 6rem;
  font-family: 'Montserrat';
}

.contact_container p {
  width: 50vw;
  font-size: 2rem;
  position: relative;
  top:-6rem;
  opacity: 0.4;
}

.contact_container .social_links {
  font-size: 2rem;
  color: white;
  position: relative;
  top:-4rem;
}

.contact_container li {
  display:inline-flex;
  padding-right: 3rem;
}

.contact_container li a {
  color:yellow;
  text-decoration: none;
  font-size: 4rem;
}

.contact_container li a:hover {
  background-color:grey;
}




/* About */

.about_container {
  display: flex;
  flex-direction: column;
  width: 80vw;
  justify-content: center;
  position: relative;
  top:10rem;
}

.about_container h2 {
  font-size: 2rem;
  font-family: 'Montserrat';
}

.about_container p {
  position: relative;
  top:-2rem;
  font-size: 1.6rem;
  width: 60vw;
  opacity: 0.4;
}

.about_container .about_contact {
  position: relative;
  top:-20rem;
}


@media only screen and (max-width: 600px) {
  body{
    font-size: 2px;
  }
  /* Header */

  .header_container {
    width: 80vw;
    top: 2rem;
  }

  .header_container h1 {
    font-size: 1rem;
  }


  .header_container ul li {
    padding-right: 1rem;
  }

  /* Home */

.home_container {
  top: 6rem;
}

.home_container .home_head_wrapper {
  top: 1.5rem;
  width: 60vw;
}

.home_container .home_head_wrapper h1 {
  font-size: 1.2rem;
}


.home_container .home_head_wrapper p {
  top: 0rem;
  font-size: 0.7rem;
}

.home_container .image_container img {
  height: 10rem;
  width: 8rem;
  top: 2rem;
}


/* Work */
.projects_container {
  top: 10rem;
  width: 80vw;
}

.projects_container h1 {
  font-size: 2rem;
}

.projects_container .myWorks_container {
  grid-gap: 0.2rem;
}

.projects_container .myWorks_container img {
  height: 8rem;
  width: 8rem;
}

.projects_container .myWorks_container .title {
  font-size: 0.8rem;
}

 .links_container  {
 color:black;
 font-size: medium;
}


/* Contact */

.contact_container {
  top: 10rem;
}

.contact_container h1 {
  font-size: 4rem;
}

.contact_container p {
  font-size: 0.6rem;
  top: -1rem;
}

.contact_container .email_link {
  font-size: 1rem;
  top: 0rem;
}

.contact_container li {
  padding-right: 1rem;
}

.contact_container li a {
  font-size: 0.5rem;
}


}


